
.profile{
margin-top: 100px;
max-width: 1200px;
border: 1px solid black;
}

.avatar{
width: 100px;
margin-left: 123px;
margin-top: 20px;
border-radius: 50%;
border: 1px solid black;
}

.username{
display: flex;
justify-content: center;
font-size: 30px;
margin-top: 10px;
margin-bottom: 10px;
color: black;
}
.tag{
display: flex;
justify-content: center;
color:darkgrey;
font-size: 20px;
line-height: 19px;
margin-bottom: 10px;
}

.location{
display: flex;
justify-content: center;
color: darkgrey;
font-size: 20px;
margin-bottom: 30px;
}

.stats{

padding: 0;

list-style-type: none;
margin: auto;
display: flex;
justify-content: center;
background-color: #a5a3a336;
}

.card{
width: 50px;
padding: 30px 32px;
display: flex;
flex-direction: column;
border: 1px solid rgb(150, 146, 146);

}

.label{
 color: darkgrey;
 margin-bottom: 5px;
font-size: 20px;
 display: flex;
 justify-content: center;

}

.quantity{
font-size: 16px;
font-weight: 700;
color: black;
 display: flex;
 justify-content: center;
}